.img_banner_apropos {
    margin: auto;
    max-width: 1440px;
    height: 250px;
    display: flex;
    flex-direction: column;
    padding: 0 10px;
    box-sizing: border-box;
    img{
        box-sizing: border-box;
        height: 250px;
        @media screen and (max-width: 768px) {
            height: 150px;
        }
    }
    @media screen and (max-width: 768px) {
        height: 150px;
    }
}

.desc {
    display: flex;
    flex-direction: column;
    width: 60%;
    margin: 30px auto;
    @media screen and (max-width: 768px) {
        margin: 30px auto 20px;
    }
}